(function() {
    'use strict';

    angular
        .module('core.app')
        .directive('acmeNavbar', acmeNavbar);

    /** @ngInject */
    function acmeNavbar() {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/layout/navbar/navbar.tpl.html',
            scope: {},
            controller: NavbarController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function NavbarController($element, $mdSidenav) {
            var vm = this;
            
            vm.sidenav = $mdSidenav;

            // "vm.creation" is avaible by directive option "bindToController: true"
            vm.relativeDate = moment(vm.creationDate).fromNow();

            //
            // Fixed on scroll
            // 
            var wdow = $(window),
                elem = $($element[0]),
                currentState = false;

            // Bind window scroll
            wdow.bind('scroll', navbarPosition);

            // Fix / unfix navbar
            function navbarPosition() {
                var pos = wdow.scrollTop();

                if (currentState !== 'add' && pos > 220) {
                    currentState = 'add';
                    elem.hide().slideDown('slow').addClass('fixed');
                } else if (currentState !== 'remove' && pos < 220) {
                    currentState = 'remove';
                    elem.hide().show().removeClass('fixed');
                }
            }
        }
    }

})();
