(function() {
    'use strict';
    /* global moment */
    /**
     * @ngdoc object
     * @name core.app.controller:BodyCtrl
     * @description
     * Controlador da aplicação client
     **/
    angular.module('core.app').controller('BodyCtrl', /*@ngInject*/ function($rootScope, $scope, $timeout, $page, $http, $state, $app, $user, $location, $menu, $mdSidenav, lodash, api, setting) {
        var body = this,
            _ = lodash,
            user = $user.instance();
        //
        // Se nao tiver instancia de usuario, reboot
        //
        //
        if (user && !user.id) {
            window.localStorage.removeItem(setting.slug + '.user');
            window.localStorage.removeItem(setting.slug + '.session_token');
            $location.path($location.url());
        }
        //
        // Comportamentos para quando o usuário sair
        //
        $rootScope.$on('$UserLeft', function() {
            //
            // Destruir storage do carrinho apenas se NÃO estiver desautorizado (sem sessão)
            // senão (caso desautorizado) manter instancia do carrinho
            // para quando houver redirect, o usuário ter suas informações de volta
            //                        
            if (!$rootScope.$Unauthorized || $rootScope.$Unauthorized === false) {
                // product.storage('session').destroy();
                // cart.storage('session').destroy();
            }
            //
            // Anulando o company id
            //
            body.companyid = false;
        });
        //
        // Boot on $broadcast
        //  
        $rootScope.$on('$stateChangeStart', function(event) {
            bootstrap();
        });
        $rootScope.$on('$stateChangeSuccess', function() {
            //
            // Auto Scroll to Top when state changed
            //
            $timeout(function() {
                $("md-content.layout-content").animate({
                    scrollTop: 0
                }, 100);
            }, 1000)
        });
        
        //
        // Boot
        // Atualmente bootando somente pelo broadcast $stateChangeStart
        // para que haja reinicialização de alguns comportamentos
        //  
        // bootstrap();
        function bootstrap() {
            //
            // Setar menu toolbar no template
            //
            body.toolbarMenu = $page.config.toolbarMenu;
            // cart.setting.support = setting.support;
            //
            // Sidenav Company Admin links
            //
            // var companyid = user.getCompany().id;
            // if (companyid) {
            //     body.companyid = companyid;
            //     //
            //     // FAQ Adm
            //     //
            //     if (_.findIndex($menu.main, {
            //         'name': 'FAQ'
            //     }) === -1) {
            //         $menu.main.push({
            //             name: 'FAQ',
            //             type: 'link',
            //             iconMi: 'styles',
            //             url: '/faq/manage/',
            //             state: 'app.faq-manage'
            //         });
            //     }
            // }
        }

        //
        // Botão voltar da toolbar de landing pegando ID quem está na URL
        // Issue: https://github.com/esgrupo/credenciando/issues/147
        // 
        body.backFunc = backFunc;

        function backFunc(route) {
            // Selecionar item da url
            $state.go(route, $state.params)
        }
    })
})();
