(function() {
    'use strict';
    angular.module('core.gmap').directive('gmap', directive);

    /*@ngInject*/
    function directive(gmapInit, $timeout) {

        var directive = {
            restrict: 'A',
            link: link
        };

        return directive;

        // Logic
        function link(scope, element, attrs) {
            gmapInit.load().then(function() {
                startMap(element);
            });
        }

        function startMap(element) {
            var geocoder = new google.maps.Geocoder;
            var infowindow = new google.maps.InfoWindow;
            
            geocoder.geocode({
                'placeId': 'ChIJb_gY6CU-uAARWmX1ZTyMAHg'
            }, function(results, status) {
                if (status === google.maps.GeocoderStatus.OK) {
                    if (results[0]) {

                        var map = new google.maps.Map(element[0], {
                            center: results[0].geometry.location,
                            zoom: 17
                        });

                        var marker = new google.maps.Marker({
                            map: map,
                            position: results[0].geometry.location
                        });
                        
                        infowindow.setContent(results[0].formatted_address);
                        infowindow.open(map, marker);
                    } else {
                        window.alert('No results found');
                    }
                } else {
                    window.alert('Geocoder failed due to: ' + status);
                }
            });
        }
    }
})();
(function() {
    'use strict';
    angular.module('core.gmap').factory('gmapInit', factory);

    // 
    // Gmap key
    // 
    var key = 'AIzaSyCINENGCI6kIl-pRV-vzNl3uSMBimMd1sk';

    /*@ngInject*/
    function factory($window, $q) {

        return {
            load: loadGmap
        };

        //
        // loadGmap -
        // will generate script tag with url and key..
        // append to the body..
        // return promise with gmap loaded
        // 
        function loadGmap() {

            var asyncUrl = 'https://maps.googleapis.com/maps/api/js?key=' + key + '&libraries=places&signed_in=true&callback=', // API url with key
                mapsDefer = $q.defer(), // Start promise
                hasGoogle = (typeof google === 'undefined' || !google); // Check if google is loaded

            //
            // Check if google api is already loaded
            // 
            if (hasGoogle) {
                // load google
                loadGoogle(mapsDefer, asyncUrl);
            } else {
                // already loaded, just solve the promise
                mapsDefer.resolve();
            }

            // Return promise
            return mapsDefer.promise;
        }

        //
        // loadGoogle -
        // start google,
        // generate and append script,
        // resolve promise
        // 
        function loadGoogle(mapsDefer, asyncUrl) {
            //Callback function - resolving promise after maps successfully loaded
            $window.googleMapsInitialized = mapsDefer.resolve; // removed ()

            //Start loading google maps
            loadScript(asyncUrl, 'googleMapsInitialized');
        }

        //
        // Async loader -
        // Generate and append script tag in the body
        // 
        function loadScript(asyncUrl, callbackName) {
            var script = document.createElement('script');

            script.async = true;
            script.defer = true;
            script.src = asyncUrl + callbackName;
            document.body.appendChild(script);
        }
    }
})();
