(function() {
    'use strict';
    angular.module('core.contact').service('Contact', /*@ngInject*/ function($http, $page, $state, $q, api, Busy) {

        var url = api.url + '/api/contacts/';
        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this.busy = new Busy.service();

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);

        };

        this.service.prototype.create = create;
        this.service.prototype.formStatus = formStatus;
        this.service.prototype.subjects = subjects;
        this.service.prototype.negativePromise = negativePromise;

        function create() {
            // Are we busy?
            if (this.busy.start('create')) return this.negativePromise();

            //
            // Limpar o model e separar outros dados
            // 
            var captcha = this.captcha,
                model = cleanModel(this);

            return $http
                .post(url, {
                    model: model,
                    captcha: captcha
                })
                .success(
                    //
                    // success
                    //
                    function(response) {
                        $page.toast('Contato enviado com sucesso, responderemos em breve!');
                        this.busy.end('create');

                        return response;
                    }.bind(this))
                .error(
                    //
                    // fail
                    //
                    function(response) {
                        $page.toast(response && response.error ? response.error : 'não foi possível enviar a mensagem, tente novamente!');
                        this.busy.end('create');

                        return response;
                    }.bind(this));
        }

        function formStatus(method, invalid) {
            return false; // testing
            return (this.busy(method) || invalid);
        }

        function subjects() {
            return [{
                value: "Contato",
                name: "Contato"
            }, {
                value: "Proposta",
                name: "Proposta"
            }, {
                value: "Sugestão",
                name: "Sugestão"
            }, {
                value: "Outros",
                name: "Outros"
            }];
        }

        function negativePromise() {
            // Start promise
            var defer = $q.defer();

            // Reject promise
            defer.reject(false);

            // Return promise
            return defer.promise;
        }

        function cleanModel(model) {
            if (!model) return model;

            var newModel = $.extend({}, model);

            newModel.busy = undefined;
            newModel.captcha = undefined;

            var clone = $.extend({}, newModel);
            return clone;
        }
    });
})();
(function() {
    'use strict';
    angular.module('core.contact').controller('ContactCtrl', /*@ngInject*/ function controller($scope, $page, $http, setting, gmapInit, vcRecaptchaService, Contact) {
        var vm = this;

        // SEO
        $page.title(setting.name + setting.titleSeparator + 'Contato');
        $page.description('Entre em contato com Matheus Davidson para desenvolvimento de sites, e-commerce e aplicações web');

        // console.log(new Contact.service());
        vm.contact = new Contact.service();

        // vm.contact.isBusyOrMake();
        // return false;
        // 
        // Contact service
        // vm.contact = new Contact;

        //
        // Subject
        // 
        vm.subjects = vm.contact.subjects();

        //
        // ReCaptcha
        // 
        vm.rcKey = '6LfR1g0TAAAAAFrMY2FgxFPIL-no4I05oumJQKvu';
        vm.widgetId = null;
        vm.contact.captcha = null;

        vm.setCaptcha = setCaptcha;
        vm.setWidgetId = setWidgetId;
        vm.cbExpiration = cbExpiration;
        vm.submit = submit;

        function setCaptcha(captcha) {
            console.info('Captcha available', captcha);
            vm.contact.captcha = captcha;
        }

        function setWidgetId(widgetId) {
            console.info('Created widget ID: %s', widgetId);
            vm.widgetId = widgetId;
        }

        function cbExpiration() {
            console.info('Captcha expired. Resetting captcha object');
            vm.contact.captcha = null;
        }

        function submit() {

            // Send contact to server with recaptcha
            vm.contact.create().then(success, fail);

            function success(response) {

            }

            function fail(response) {
                // Reload captcha
                vcRecaptchaService.reload(vm.widgetId);
                console.log('response', response);
                console.log('falhou');
            }
        }
    });
})();
